import {Champignon, ChampignonEnum} from "./Champignon";

export type Perso = {
    // 0 (repus)
    //  20 (mort de faim)
    // -1 par tick, +6 par champi (??)
    faim: number;
    cycle_passe: number;
    nuit: boolean;
    distanceParcourue: number; // 0  DISTANCE_COMPLETE
    vitesseExecution: number, // en secondes entre chaque vnement
    mort?: boolean,
    victoire?: boolean,
    debogue: boolean;
    forme: Forme;

    // caracs gameplay visibles
    vitesse: number; // nombre de distance parcourue par tick (vitesseExecution)
    niveau:number;
    champignons: ChampignonEnum[];
    // champignons ingrs qui font de l'effet pour un temps chronomtr
    digestion: Champignon[];
};


export enum Forme {
    humain = "Human",
    escargot = "Snail",
    lynx = "Faerie",
    sanglier = "Boar",
}