import React, {useCallback, useContext, useEffect, useState} from "react";
import {PersoContexte, PersoContexteType} from "../contexte/ContexteType";
import {Box, Button, Grid, Typography} from "@mui/material";
import Decor from "./Decor";
import BoucleEvts from "./BoucleEvts";
import {createTheme, ThemeProvider} from "@mui/material/styles";
import Boutons from "./Boutons";
import AffichageDigestion from "./AffichageDigestion";
import Progression from "./Progression";
import {GameOverOverlay} from "./GameOverOverlay";
import {initPerso} from "../App";
import {useSound} from "react-sounds";
import musique_nuit from '../donnees/sons/musique_nuit.mp3';
import musique_jour from '../donnees/sons/musique_jour.mp3';
import {fond} from "../donnees/images";

const theme = createTheme({
    palette: {
        primary: {
            main: '#4caf50', // Vert pour le thme principal
        },
    },
});

export default function Jeu() {
    const [afficherMenu, setAfficherMenu] = useState(true);
    const [messageFondu, setMessageFondu] = useState<string>('');
    const { perso, setPerso } = useContext(PersoContexte) as PersoContexteType;
    const { play:play_musique_nuit, stop:stop_musique_nuit} = useSound(musique_nuit);
    const { play:play_musique_jour, stop:stop_musique_jour } = useSound(musique_jour);

    const onRestart = useCallback(() => {
        console.log("Mathieu onRestart initPerso : ", initPerso);
        setPerso({
            ...initPerso,
            champignons: [],
            digestion: [],
        })
    }, [setPerso]);

    useEffect(() => {
        if (!afficherMenu) {
            play_musique_jour();
        }
    }, [afficherMenu]);

    useEffect(() => {
        if (!perso.mort && !perso.victoire) {
            if (perso.nuit) {
                stop_musique_jour();
                play_musique_nuit();
            } else {
                stop_musique_nuit();
                play_musique_jour();
            }
        }
    }, [perso.mort, perso.nuit, perso.victoire/*, play_musique_jour, play_musique_nuit, stop_musique_jour, stop_musique_nuit*/]);

    return (
        <ThemeProvider theme={theme}>
            {afficherMenu ? (
                        <Box
                            sx={{
                                height: '100vh',
                                display: 'flex',
                                justifyContent: 'center',
                                alignItems: 'center',
                                flexDirection: 'column',
                                backgroundImage: `url(${fond})`, // Utilisation de l'image de fond
                                backgroundRepeat: 'repeat-x',
                                backgroundSize: 'cover', // ou 'contain' selon l'effet souhait
                                backgroundPosition: 'center',
                            }}
                        >
                            <Box
                                sx={{
                                    height: '60vh',
                                    width: '80vh',
                                    display: 'flex',
                                    justifyContent: 'center',
                                    alignItems: 'center',
                                    backgroundColor: '#76fb7d',
                                    borderRadius: '16px', // Arrondi des bords
                                    padding: '24px', // Ajout de padding
                                    flexDirection: 'column',
                                    boxSizing: 'border-box', // Pour que le padding soit inclus dans la largeur/hauteur
                                }}
                            >
                                <Typography
                                    variant="h3" // Rduction de la taille du titre
                                    component="h1"
                                    sx={{
                                        color: '#1a4d2e', // Vert trs fonc
                                        fontWeight: 'bold',
                                        textAlign: 'center', // Centrage du titre
                                        textShadow: '2px 2px 4px rgba(0, 0, 0, 0.5)',
                                        mb: 2, // Marge en bas pour espacer le titre du texte
                                    }}
                                >
                                    Forest of the magic mushrooms
                                </Typography>
                                <Typography
                                    component="ul" // Utilisation d'une liste HTML
                                    sx={{
                                        color: '#1a4d2e', // Vert trs fonc
                                        pl: 2, // Padding  gauche pour les puces
                                        m: 0, // Suppression des marges par dfaut
                                    }}
                                >
                                    <Typography component="li" sx={{ mb: 1 }}>
                                        You are lost in a magical forest. Your only chance of survival is mushrooms. Many delicious and strange mushrooms to collect and consume.
                                    </Typography>
                                    <Typography component="li" sx={{ mb: 1 }}>
                                        you will automatically pick mushrooms along the way
                                    </Typography>
                                    <Typography component="li" sx={{ mb: 1 }}>
                                        click on the buttons of the mushrooms in order to feed and trigger an effect (maybe)
                                    </Typography>
                                    <Typography component="li" sx={{ mb: 1 }}>
                                        get out of the forest (without dying of hunger)
                                    </Typography>
                                </Typography>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    size="large"
                                    sx={{
                                        backgroundColor: '#ff9800',
                                        color: 'white',
                                        fontSize: '1.2rem',
                                        padding: '12px 36px',
                                        '&:hover': {
                                            backgroundColor: '#e68a00',
                                        },
                                        mt: 2, // Marge en haut pour espacer le bouton
                                    }}
                                    onClick={() => {
                                        setAfficherMenu(false);
                                    }}
                                >
                                    lets pick mushrooms!
                                </Button>
                            </Box>

                        </Box>

            ) : (
                <Box
                    sx={{
                        height: '100vh',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        flexDirection: 'column',
                        backgroundImage: `url(${fond})`, // Utilisation de l'image de fond
                        backgroundRepeat: 'repeat-x',
                        backgroundSize: 'cover', // ou 'contain' selon l'effet souhait
                        backgroundPosition: 'center',
                    }}
                >
                <Grid container sx={{ height: 600, width: 800 }}>
                    <Grid size={4}>
                        <AffichageDigestion />
                    </Grid>
                    <Grid size={8}>
                        <Box
                            sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                height: '100vh', // Prend toute la hauteur de la fentre
                                overflow: 'hidden', // Dsactive le scroll pour le parent
                            }}
                        >
                            <Decor messageFondu={messageFondu}/>
                            <Progression/>
                            <Box sx={{
                                maxHeight: '180px',
                                height: '180px',
                                padding: 1,
                                flexGrow: 1, // Prend l'espace restant
                                overflow: 'auto', // Active le scroll uniquement ici
                            }}>
                                <Boutons setMessageFondu={setMessageFondu}/>
                            </Box>
                            <Box sx={{
                                padding: 1,
                                overflow: 'auto', // Active le scroll uniquement ici
                                backgroundColor: '#71f178',
                                maxHeight: '180px',
                                height: '180px',
                                borderRadius: '16px', // Optionnel : coins arrondis
                            }}>
                                <BoucleEvts/>
                            </Box>
                        </Box>
                    </Grid>
                </Grid>
                </Box>
            )}
            {(perso.mort || perso.victoire) && <GameOverOverlay onRestart={onRestart} mort={perso.mort ?? false}/>}
        </ThemeProvider>
    );
}
